import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../public/services/auth.service';
import { GeneralService } from '../../public/services/general.service';

@Component({
  selector: 'ngx-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  /* Binding */
  private bin: any = {};
  public loading: boolean = false;

  /** API */
  private readonly API_AUTH: string = 'users/authenticate';

  constructor(
    private authService: AuthService,
    private generalService: GeneralService,
  ) {}

  ngOnInit() {}

  // =================================
  // Metodos
  // ================================

  /**
   * Autenticacion de usuario
   */
  public onSubmit(): void {
    if (!(this.bin.email && this.bin.password)) {
      this.generalService.onIncomplete();
      return;
    }

    const user: any = {
      email: this.bin.email,
      password: this.bin.password,
    };
    this.loading = true;
    const titulo: string = 'Autenticación de usuario.';
    this.authService
      .post({ api: this.API_AUTH, data: user })
      .then((response) => {
        this.loading = false;
        if (response) {
          if (response.user.tipo === 'administrador' && response.user.empresa.estado !== 'aprobado') {
            this.generalService.showToastr({
              title:
                'Tu solicitud está pendiente o rechazada, por favor comunícate con el administrador de la plataforma',
              status: 'warning',
            });
          } else {
            if (this.authService.setAuthValues({ data: {token: response.token, user: response.user} })) {
              if (this.authService.isSuperadmin) {
                this.bin = [];
                this.generalService.routerNavigateTo({
                  path: '/index/dashboard',
                });
              } else {
                this.authService.logout();
                this.generalService.showToastr({
                  title:
                    'No tiene permiso para acceder a la plataforma administrativa.',
                  status: 'danger',
                });
              }
            }
          }
        }
      });
  }

  /**
   * Olvido la contraseña
   */
  public onForgot(): void {
    this.generalService.routerNavigateTo({ path: '/login/forgot' });
  }

  public onRegister() {
    this.generalService.routerNavigateTo({ path: '/login/register' });
  }
}
