import { Component, OnInit } from "@angular/core";
import { AuthService } from "../../public/services/auth.service";
import { GeneralService } from "../../public/services/general.service";

@Component({
  selector: "ngx-forgot",
  templateUrl: "./forgot.component.html",
  styleUrls: ["./forgot.component.scss"],
})
export class ForgotComponent implements OnInit {
  /** Binding */
  private bin: any = {};
  public loading: boolean = false;

  /** API */
  static readonly ROUTE: string = "login/forgot";
  private readonly API_FORGOT: string = "users/forgot-password";

  constructor(
    private authService: AuthService,
    private generalService: GeneralService
  ) {}

  ngOnInit() {}

  // =================================
  // Metodos
  // ================================

  /**
   * Autenticacion de usuario
   */
  public onSubmit(): void {
    if (!this.bin.email) {
      this.generalService.onIncomplete();
      return;
    }

    const user: any = {
      email: this.bin.email,
    };

    this.loading = true;
    const titulo: string = "Cambio de clave.";
    this.authService
      .post({ api: this.API_FORGOT, data: user })
      .then((response) => {
        this.loading = false;
        if (response) {
          this.generalService.showToastr({
            title: "Correo enviado. Revisa la bandeja de entrada o de spam.",
            message: `${this.bin.email}`,
            status: "success",
          });
          this.bin = {};
        }
      });
  }

  /**
   * Olvido la contraseña
   */
  public onLogin(): void {
    this.generalService.routerNavigateTo({ path: "/login" });
  }
}
