import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../public/services/auth.service';
import { GeneralService } from '../../public/services/general.service';
import { MainService } from '../../public/services/main.service';
import { S3Service } from '../../public/services/s3.service';

@Component({
  selector: 'ngx-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss'],
})
export class RegisterComponent implements OnInit {
  /* Binding */
  private empresa: any = {};
  private user: any = { tipo: 'administrador' };
  public step: number = 0;
  public sectores = [];
  public selectedFile;
  public loading: boolean = false;

  /** API */
  private readonly API_REGISTER: string = 'users/register';
  private readonly API_AUTHENTICATE: string = 'users/authenticate';
  private readonly API_SECTOR: string = 'api/sector';
  private readonly API_EMPRESA: string = 'api/empresa';
  private readonly API_VERIFY_EMAIL: string = 'users/verify-email';

  constructor(
    private authService: AuthService,
    private mainService: MainService,
    private generalService: GeneralService,
    private s3: S3Service,
  ) {}

  ngOnInit() {
    this.getSectores();
  }

  // =================================
  // Metodos
  // ================================

  // =================================
  // Metodos
  // ================================

  /**
   * Fetchs sectores from backend
   */
  public getSectores() {
    this.mainService.get({ api: this.API_SECTOR }).then((response) => {
      this.sectores = response as [];
    });
  }

  /**
   * Registers the empresa and the user
   */
  public onSubmit(): void {
    const callback = (err, data) => {
      if (err) {
        // If there is an error alert the user
        alert('No fue posible subir el archivo, por favor intente más tarde');
      } else {
        const imgUrl = data.Location;
        this.register({ logo: imgUrl });
      }
    };
    this.uploadFile({ file: this.selectedFile, callback });
  }

  public register({logo}) {
    if (
      this.user &&
      this.user.nombre &&
      this.user.apellido &&
      this.user.email &&
      this.user.password
    ) {
      this.empresa['logo'] = logo ? logo : '';
      this.mainService
        .post({ api: this.API_EMPRESA, data: this.empresa })
        .then((resEmpresa) => {
          const eid = resEmpresa._id;
          this.user['empresa'] = eid;
          this.authService
            .post({ api: this.API_REGISTER, data: this.user })
            .then((resUser) => {
              if (!resUser) {
                this.mainService
                  .delete({ api: `${this.API_EMPRESA}/${eid}` })
                  .then((del) => {
                  }).finally(() => {
                    this.generalService.handleError({
                      error: 'Hubo un error en el registro, por favor intente de nuevo',
                      titulo: 'Error',
                    });
                  });
              } else {
                this.step = 2;
              }
            });
        });
    } else {
      this.generalService.handleError({
        error: 'Por favor llena todos los campos',
        titulo: 'Error',
      });
    }
  }

  /**
   * Handle back
   */
  public onLogin(): void {
    this.generalService.routerNavigateTo({ path: '/login' });
  }

  /**
   * Opens logo file selector
   */
  public openFileSelector() {
    document.getElementById('logo-selector').click();
  }

  /**
   * Handles click on next step
   */
  public nextStep() {
    if (
      this.empresa &&
      this.empresa.nombre &&
      this.empresa.sectores &&
      this.empresa.sectores.length > 0 &&
      this.empresa.descripcion
    ) {
      this.step++;
    } else {
      this.generalService.handleError({
        error: 'Por favor llena todos los campos',
        titulo: 'Error',
      });
    }
  }

  /**
   * Handles click on go back
   */
  public previousStep() {
    this.step--;
  }

  /**
   * Listens on change of select file
   * @param param0 with event of the input
   */
     public onChangeMedia({ event }) {
      if (event.target.files.length > 0) {
        this.selectedFile =  event.target.files[0];
      }
    }

    /**
   * Uploads pictures to S3 in a folder
   * @param file: File object to upload, callback function to execute
   */
  async uploadFile({ file, callback }) {
    const empresaName = this.empresa.nombre.replace(' ', '-');
    const key = `empresa/${empresaName || 'no-name'}/logo-${this.s3.generateKey()}`;
    await this.s3.uploadFile({ file, key, callback });
  }
}
